<script>
export default {
  computed: {
    myYear () {
      return this.valueMonth.slice(0, 4)
    },
    myMonth () {
      return this.valueMonth.slice(5, 7)
    }
  },
  data () {
    return {
      monthCountData: [],
      monthQualityData: [],
      monthAutoRate: [],
      monthLeastInTime: [],
      valueMonth: '',
      top10NE1: [],
      top10NE2: [],
      top10NE3: [],
      top10NE4: [],
      top10NE5: [],
      top10NE6: [],
      top10NE7: [],
      top10NE8: [],
      link: 'http://132.96.194.30:8078/report/top10ne/export/'
    }
  },

  methods: {
    table_monthCount1 () { // 表1
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/top10ne/' + this.myYear + '/' + this.myMonth
      }).then(response => {
        this.top10NE1 = response.data.result.Net_4G
        // console.log(response.data)
      }).catch(error => {
        console.log(error)
      })
    },
    table_monthCount2 () { // 表2
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/top10ne/' + this.myYear + '/' + this.myMonth
      }).then(response => {
        this.top10NE2 = response.data.result.Repeater
      }).catch(error => {
        console.log(error)
      })
    },
    table_monthCount3 () { // 表3
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/top10ne/' + this.myYear + '/' + this.myMonth
      }).then(response => {
        this.top10NE3 = response.data.result.Net_CDMA
      }).catch(error => {
        console.log(error)
      })
    },
    table_monthCount4 () { // 表4
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/top10ne/' + this.myYear + '/' + this.myMonth
      }).then(response => {
        this.top10NE4 = response.data.result.Transmission
      }).catch(error => {
        console.log(error)
      })
    },
    table_monthCount5 () { // 表5
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/top10ne/' + this.myYear + '/' + this.myMonth
      }).then(response => {
        this.top10NE5 = response.data.result.Net_Optical
      }).catch(error => {
        console.log(error)
      })
    },
    table_monthCount6 () { // 表6
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/top10ne/' + this.myYear + '/' + this.myMonth
      }).then(response => {
        this.top10NE6 = response.data.result.Dynamics
      }).catch(error => {
        console.log(error)
      })
    },
    table_monthCount7 () { // 表7
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/top10ne/' + this.myYear + '/' + this.myMonth
      }).then(response => {
        this.top10NE7 = response.data.result.Exchange
      }).catch(error => {
        console.log(error)
      })
    },
    table_monthCount8 () { // 表8
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/top10ne/' + this.myYear + '/' + this.myMonth
      }).then(response => {
        this.top10NE8 = response.data.result.Data
      }).catch(error => {
        console.log(error)
      })
    },
    draw_monthDistribution () { // 图1
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/amountcompare/' + this.myYear + '/' + this.myMonth
      }).then(response => {
        var cityList = []
        // var data1 = [ this.myMonth - 1, this.myMonth ]
        // var legend = {data: data1}
        var lmAmountValueList = []
        var AmountValueList = []
        var data = response.data.result
        var l = response.data.result.length
        for (var i = 0; i < l; i++) {
          cityList.push(data[i]['city'])
          lmAmountValueList.push(data[i]['sum_amount_lm'])
          AmountValueList.push(data[i]['sum_amount'])
        }
        let myChart = this.$echarts.init(document.getElementById('chart_distribution'))
        var option = {
          color: ['#ed7d31', '#5c9ad5'],
          title: {
            text: '故障单分布情况'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          legend: {
            data: ['上月', '本月']
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            top: '15%',
            containLabel: true
          },
          yAxis: {
            type: 'value',
            boundaryGap: [0, 0.01]
          },
          xAxis: {
            type: 'category',
            data: cityList
          },
          series: [
            {
              name: '上月',
              type: 'bar',
              data: lmAmountValueList
            },
            {
              name: '本月',
              type: 'bar',
              data: AmountValueList
            }
          ]
        }
        // option.legend.data = legend
        myChart.setOption(option)
      }
      )
    },
    draw_monthReason () { // 图2
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/districtreason/' + this.myYear + '/' + this.myMonth
      }).then(response => {
        if (response.data.status) {
          var reasonList = []
          var WestValueList = []
          var EastValueList = []
          var NorthValueList = []
          var PRD1ValueList = []
          var PRD2ValueList = []
          var data = response.data.result
          var l = response.data.result.length
          for (var i = 0; i < l; i++) {
            reasonList.push(data[i]['profession'])
            WestValueList.push(data[i]['GD_W'])
            EastValueList.push(data[i]['GD_E'])
            NorthValueList.push(data[i]['GD_N'])
            PRD1ValueList.push(data[i]['PRD_1'])
            PRD2ValueList.push(data[i]['PRD_2'])
          }
          let myChart = this.$echarts.init(document.getElementById('chart_reason'))
          myChart.setOption({
            color: ['#5b9cd6', '#ed7d31', '#a6a6a6', '#ffbf01', '#4473c5'],
            title: {text: '故障原因归类'},
            tooltip: {
              trigger: 'axis',
              axisPointer: {type: 'shadow'}
            },
            legend: {
              show: true,
              data: ['珠1', '珠2', '粤西', '粤东', '粤北']
            },
            grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
            yAxis: {
              type: 'value'
            },
            xAxis: {
              type: 'category',
              data: reasonList
            },
            series: [
              {
                name: '珠1',
                type: 'bar',
                barWidth: 50,
                stack: '总量',
                label: {
                  normal: {
                    show: true,
                    position: 'insideRight'
                  }
                },
                data: PRD1ValueList
              },
              {
                name: '珠2',
                type: 'bar',
                barWidth: 50,
                stack: '总量',
                label: {
                  normal: {
                    show: true,
                    position: 'insideRight'
                  }
                },
                data: PRD2ValueList
              },
              {
                name: '粤西',
                type: 'bar',
                barWidth: 50,
                stack: '总量',
                label: {
                  normal: {
                    show: true,
                    position: 'insideRight'
                  }
                },
                data: WestValueList
              }, {
                name: '粤东',
                type: 'bar',
                barWidth: 50,
                stack: '总量',
                label: {
                  normal: {
                    show: true,
                    position: 'insideRight'
                  }
                },
                data: EastValueList
              }, {
                name: '粤北',
                type: 'bar',
                barWidth: 50,
                stack: '总量',
                label: {
                  normal: {
                    show: true,
                    position: 'insideRight'
                  }
                },
                data: NorthValueList
              }
            ]
          })
        }
      }
      )
    },
    draw_monthSituation () { // 图3
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/cityrate/'
      }).then(response => {
        if (response.data.status) {
          var cityList = []
          var SignValueList = []
          var AutoValueList = []
          var DealValueList = []
          var data = response.data.result
          var l = response.data.result.length
          for (var i = 0; i < l; i++) {
            cityList.push(data[i]['city'])
            SignValueList.push(data[i]['signRate'])
            AutoValueList.push(data[i]['autoRate'])
            DealValueList.push(data[i]['dealRate'])
          }
          let myChart = this.$echarts.init(document.getElementById('chart_situation'))
          myChart.setOption({
            title: {
              text: '各地市指标情况'
            },
            tooltip: {
              trigger: 'axis'
            },
            legend: {
              data: ['签单及时率', '自动派单率', '故障处理及时率']
            },
            xAxis: {
              type: 'category',
              data: cityList
            },
            yAxis: {
              type: 'value',
              axisLabel: {
                show: true,
                interval: 'auto',
                formatter: '{value} %'
              },
              min: 65,
              max: 100
            },
            series: [{
              name: '签单及时率',
              data: SignValueList,
              type: 'line'
            }, {
              name: '自动派单率',
              data: AutoValueList,
              type: 'line'
            }, {
              name: '故障处理及时率',
              data: DealValueList,
              type: 'line'
            }]
          })
        }
      })
    },
    table_monthLeastTimly () { // 表10
      this.$http({
        method: 'get',
        url: 'http://132.96.194.30:8078/report/worst10department/' + this.myYear + '/' + this.myMonth
      }).then(response => {
        if (response.data.status) {
          this.monthLeastInTime = response.data.result
        }
      }).catch(error => {
        console.log(error)
      })
    },
    DataFilter () {
      this.table_monthCount1()
      this.table_monthCount2()
      this.table_monthCount3()
      this.table_monthCount4()
      this.table_monthCount5()
      this.table_monthCount6()
      this.table_monthCount7()
      this.table_monthCount8()
      this.draw_monthDistribution()
      this.draw_monthReason()
      this.draw_monthSituation()
      this.table_monthLeastTimly()
    }
  },
  mounted () {
  }
}
