  .main {
      margin: 0 auto;
      width: 90%;
    }
  .main h3{
      font: 18px/20px bold 微软雅黑;
      color: rgb(130,130,130);
      text-align:left;
    }
  /*.count{ width:1250px; border:1px; padding:2px; overflow:hidden ;margin: 30px 30px;}*/
  /*!*.divleft{ float:left; width:100%; border:1px }*!*/
  /*!*.divright{ float:right; width:100%; border:1px }*!*/
  .quality,.analysis,.situation{
     margin-bottom: 30px;
   }
  .el-table .cell{
    line-height: 15px;
  }
  .el-form-item{
    margin-bottom: -13px;
  }
  .el-table thead.is-group th {
    background: #4473c5 !important;
  }
  .el-table thead,.el-table--border th, .el-table__fixed-right-patch{
    color: #ffffff;
    background-color: #4473c5 !important;
  }
  .el-table_2_column_10 is-center ,el-table_2_column_10 is-center is-leaf,el-table_1_column_1 is-center {
    background-color: #4473c5 !important;
    color: #ffffff !important;
  }
</style>



